:root {
  --color-primary: lightblue;
  --color-secondary: #fff;
  --color-accent-hover: #FF8989d9;
  --color-accent: #FF898966;


  --color-blue: #698EB7;
  --color-red: #FF9D9D;
  --color-green: #ADE87D ;
  --color-button-hover: #f1f1f1;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

*:focus {
  outline: none;
}


html {
  font-size: 62.5%;
  font-family: 'Varela Round', sans-serif;
}

body {
  color: #000;
  font-size: 2rem;
  background-color: var(--color-primary);
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2rem;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 36rem;
  margin: auto;
}

.primary-header {
  width: 100%;
  display: flex;
  gap: 1rem;
  align-items: center;
  justify-content: space-between;
  margin: 2rem;
}

.primary-header_logo {
  margin: 0;
  margin-left: 0.5rem;
  font-size: 1.6rem;
}

.primary-navigation {
  display: flex;
  gap: 1.1rem;
  list-style: none;
  padding: 0;
  margin: 0;
  margin-right: 0.5rem;
  align-items: center;
}

/* Popups */
.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
}

.popup-inner {
  position: relative;
  padding: 4rem 10rem;
  width: 90%;
  max-width: 500px;
  background-color: var(--color-secondary);
  border-radius: 6px;
}

.popup-verification {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5rem;
}

/* Sections */
.section-task-container {
  display: flex;
  flex-direction: column;
  border-radius: 6px;
  padding: 1rem;
  margin-top: 2rem;
  width: 100%;
}

/* Headings */
.heading-error-text {
  text-align: center;
  font-size: 2.8rem;
}

.heading-load-template {
  margin: 0;
  font-size: 2.4rem;
}

.heading-login {
  font-size: 2.4rem;
  margin: 0 0 2rem;
}

.heading-settings {
  margin: 0 0 2rem;
}

.heading-settings-sub {
  margin: 0;
  width: 100%;
  padding: 0.5rem 0.2rem;
  border-bottom: 1px solid black; 
  font-size: 2.2rem;
}

.heading-task {
  margin: 0;
  font-size: 1.6rem;
}

.heading-task-container {
  margin: 0;
  font-size: 2.4rem;
}

.heading-time-display {
  font-size: 7.8rem;
  margin: 0;
}


/* Text */
.text-login-error {
  color: orangered;
  margin: 0 0 0.5rem;
  font-size: 1.5rem;
  align-self: center;
}

.text-error {
  color: orangered;
  margin: 0;
  font-size: 1.2rem;
  align-self: center;
}

.text-submission-error {
  color: orangered;
  margin: 0.5rem;
  font-size: 1.2rem;
  place-self: center;
}

.text-duration {
  font-size: 1.6rem;
}

.text-focus-icons {
  margin: 0;
  font-size: 0.96rem;
}

/* Icons */
.icon-focus-lemon {
  font-size: 1.2rem;
  margin-top: 0.5rem;
}

/* Buttons */
.button:hover {
  cursor: pointer;
}

.btn-close {
  position: absolute;
  top: 16px;
  right: 16px;
}

.btn-task-container-add {
  padding: 1rem 0;
  background-color: white;
  border: 1px solid grey;
  border-radius: 4px;
}
.btn-task-container-add:hover {
  cursor: pointer;
  background-color: var(--color-accent);
}

.btn-close-addTask {
  position: absolute;
  top: 1rem;
  right: 1.2rem;
  border: none;
  padding: 0.44rem 0.44rem 0.4rem;
  margin-top: 0.6rem;
  border-radius: 25%;
  border: 1px solid black;
  background-color: white;
  font-size: 1.04rem;
}

.btn-close-addTask:hover {
  background-color: var(--color-button-hover);
  cursor: pointer;
}

.btn-delete-account {
  padding: 0.4rem 1.6rem;
  font-size: 1.6rem;
  font-family: 'Varela Round', sans-serif;
  border: none;
  border-bottom: 1px solid black;
  background: none;
}

.btn-delete-account:hover {
  border-radius: 4px;
  cursor: pointer;
}

.btn-yes:hover {
  background-color: var(--color-green);
}

.btn-no:hover {
  background-color: var(--color-accent-hover);
}

.btn-edit-task-close {
  position: absolute;
  top: 1rem;
  right: 1.2rem;
  border: none;
  padding: 0.44rem 0.44rem 0.4rem;
  border-radius: 25%;
  border: 1px solid black;
  background-color: white;
  font-size: 1.04rem;
}

.btn-edit-task-close:hover {
  background-color: var(--color-button-hover);
  cursor: pointer;
}

.btn-edit-task-delete {
  position: absolute;
  bottom: 0.04rem;
  font-family: 'Varela Round', sans-serif;
  right: 5.2rem;
  border: none;
  border-bottom: 1px solid black;
  padding: 0.5rem;
  background-color: white;
  font-size: 1.04rem;
}

.btn-edit-task-delete:hover {
  border-radius: 4px;
  cursor: pointer;
  background-color: var(--color-accent-hover);
}

.btn-load-template-edit {
  border: none;
  font-family: 'Varela Round', sans-serif;
  background: none;
  border-bottom: 1px solid black;
  font-size: 1.2rem;
}

.btn-load-template-edit:hover {
  background-color: var(--color-accent);
  border-radius: 4px;
  cursor: pointer;
}

.btn-forgot-pass {
  margin-top: 1.1rem;
}

.btn-alt-form {
  border: none;
  background: none;
  text-decoration: underline;
  color: #3F50BB;
  font-family: 'Varela Round', sans-serif;
}

.btn-alt-form:hover {
  cursor: pointer;
  color: #828FE0;
}

.btn-login-close {
  border: none;
  padding: 0.55rem 0.55rem 0.2rem;
  border-radius: 25%;
  border: 1px solid black;
  background-color: var(--color-secondary);
  font-size: 1.4;
}

.btn-login-close:hover {
  background-color: var(--color-accent);
  cursor: pointer;
}

.btn-submit {
  margin: 1.6rem 0 0;
  width: 25%;
  height: 3rem;
  font-family: 'Varela Round', sans-serif;
  border: none;
  background-color: white;
  background-color: var(--color-green);
  font-size: 1.6rem;
  border-radius: 4px;
}

.btn-submit:hover {
  cursor: pointer;
  background-color: var(--color-green);
}

.btn-edit {
  align-self: center;
  border: 1px solid black;
  padding: 0.55rem 0.55rem 0.2rem;
  border-radius: 25%;
  background-color: white;
  font-size: 1.04rem;
}

.btn-edit:hover {
  background-color: var(--color-button-hover);
  cursor: pointer;
}

.btn-dropdown {
  background-color: white;
  color: black;
  font-size: 2.4rem;
  border: 1px solid gray;
  border-radius: 3px;
  cursor: pointer;
  text-align: center;
  line-height: 1rem;
}

.btn-dropdown:hover {
  background-color: var(--color-button-hover)
}

.btn-time-block {
  background: none;
  border: none;
  font-size: 1.05rem;
}

.btn-stop-and-go {
  padding: 0.6rem 1.5rem 0.3rem;
  background-color: var(--color-green);
  border: none;
  border-radius: 4px;
  font-family: 'Varela Round', sans-serif;
  font-size: 1.4rem;
}

.btn-stop {
  background-color: var(--color-accent-hover);
}

.btn-reset {
  background: none;
  border: none;
  font-size: 1.4rem;
}

.btn-skip {
  background: none;
  border: none;
  font-size: 1.4rem;
}

.btn-dropdown.user-opts {
  background: none;
  color: black;
  padding: 0.55rem 0.55rem 0.7rem;
  display: flex;
  border: none;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-radius: 0;
  border-bottom: 1px solid black;
  font-size: 1.12rem;
}

.btn-dropdown.user-opts:hover {
  background-color: var(--color-accent);
  border-radius: 4px;

}

/* Links */
.link-button {
  border: none;
  background: none;
  border-bottom: 1px solid black;
  font-size: 1.2rem;
  padding: 0.4rem;
  font-family: 'Varela Round', sans-serif;
}

.link-button:hover {
  cursor: pointer;
  background-color: var(--color-accent);
  border-radius: 4px;
}



/* Cards */
.card-add {
  border: 1px solid grey;
  border-radius: 4px;
  padding: 0.5rem 0.5rem;
  margin: 0.3rem 0;
  background-color: white;
  display: flex;
  flex-direction: column;
}

.card-edit-task {
  position: relative;
}

.card-task {
  border: 1px solid grey;
  border-radius: 4px;
  padding: 0.5rem 0.5rem;
  margin: 0.3rem 0;
  background-color: white;
  display: flex;
  flex-direction: column;
  z-index: 999;
  cursor: pointer;
}

.card-task-selected {
  margin: 1rem 0;
  padding: 0.5rem;
  background-color: var(--color-accent);
  border-radius: 6px;
  /* z-index: 100; */
}

.card-timer-container {
  display: grid;
  grid-gap: 0.5rem;
  grid-template-columns: 7.8rem 7.8rem 7.8rem;
  grid-template-rows: 1.5rem 12rem 2.4rem;
  place-items: center;
  place-content: center;
  margin: 3rem 0;
}

/* Wrappers */
.wrap-delete-account-buttons {
  display: flex;
  justify-content: center;
  gap: 2rem;
}

.wrap-edit-task {
  display: flex;
  flex-direction: column;
}

.wrap-edit-task-mainline {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0.8rem 3.92rem 0.4rem 0.64rem;
  padding: 0 0.5rem;
  height: 3rem;
}

.wrap-load-template-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.wrap-load-template-name {
  font-size: 1.6rem;
}

.wrap-login-footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 1.6rem;
}

.wrap-footer-text {
  font-size: 1.2rem;
}

.wrap-reset-success {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.wrap-settings-item {
  display: flex;
  width: 100%;
  padding: 0.5rem;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px dotted black;
  border-left: 1px solid black;
  border-right: 1px solid black;
  background-color: white;
}

.wrap-settings-item.section-end {
  border-bottom: 1px solid black;
}

.wrap-task-mainline {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0.8rem 0.8rem 0.4rem;
  padding: 0 0.5rem;
  height: 3rem;
  border-bottom: 1px dotted black;
}

.wrap-task-settings {
  display: flex;
  gap: 2rem;
  align-items: center;
  height: 3rem;
}

.wrap-task-container-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
}

.wrap-dropdown {
  position: relative;
  display: inline-block;
}

.wrap-dropdown-content {
  display: none;
  position: absolute;
  right: 0;
  background-color: #fff;
  min-width: 120px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.wrap-dropdown-content .wrap-dropdown-content-option {
  color: black;
  padding: 0.72rem 0.8rem;
}

.wrap-dropdown-content .wrap-dropdown-content-option:hover {
  background-color: #f1f1f1;
  cursor: pointer;
}

.wrap-show-dropdown-content {
  display: block;
}

.wrap-dropdown-content-option > p {
  font-size: 1.2rem;
  padding: 0.24rem;
  margin: 0;
}

.wrap-time-display {
  grid-column: span 3;
}

.wrap-timer-frame {
  background-color: var(--color-secondary);
  border: 1px solid black;
  border-radius: 50%;
  min-height: 29.4rem;
  min-width: 29.4rem;
  align-self: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;
}

.wrap-focus-icons {
  display: flex;
  gap: 0.8rem;
}

.wrap-time-block-selected {
  background: fixed;
  font-size: 1.2rem;
  font-weight: 800;
  padding: 0.3rem;
  border-radius: 4px;
  background-color: var(--color-accent);
  border-bottom: 1px solid black;
}

.wrap-pomodoro {
  place-self: center end;
}

.wrap-short-break {
  place-self: center
}

.wrap-long-break {
  place-self: center start;
}

.wrap-reset  {
  place-self: center end;
}

.wrap-skip {
  place-self: center start;
}

/* Forms */
.form-login-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.form-settings-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.input-edit-task-heading {
  font-size: 1.6rem;
  font-family: 'Varela Round', sans-serif;
  width: 16rem;
}

.input-edit-task-duration {
  font-family: 'Varela Round', sans-serif;
  font-size: 1.6rem;
  text-align: center;
  width: 4rem;
}

.input-edit-task-save {
  align-self: flex-end;
  font-family: 'Varela Round', sans-serif;
  margin: 0.8rem 1.44rem 0;
  border: none;
  border-bottom: 1px solid black;
  padding: 0.4rem;
  background-color: white;
  font-size: 1.04rem;
}

.input-edit-task-save:hover {
  cursor: pointer;
  border-radius: 4px;
  background-color: var(--color-green);
}

.input-edit-task-notes {
  padding: 0.64rem 1.2rem;
  margin: 0.4rem 0.8rem 0;
  align-self: stretch;
  background-color: whitesmoke;
  font-family: 'Varela Round', sans-serif;
  border-radius: 3px;
  resize: none;
  font-size: 1.2rem;
}

.input-login {
  margin: 0 0 0.8rem;
  padding: 0.32rem;
  height: 2.8rem;
  width: 100%;
  font-size: 1.44rem;
  font-family: 'Varela Round', sans-serif;
}

.input-error {
  border: 1px solid orangered;
  box-shadow: 0 1px 5px orangered;
}

.input-label-settings {
  font-size: 1.5rem;
}

.input-settings {
  padding: 0 0.4rem;
  height: 3rem;
  width: 5rem;
  font-size: 1.5rem;
  text-align: center;
  font-family: 'Varela Round', sans-serif;
  border: none;
  background-color: var(--color-secondary);
  border-radius: 4px;
}

.input-settings-checkbox {
  cursor: pointer;
}

.input-task-notes {
  padding: 0.64rem 1.2rem;
  margin: 0.4rem 0.8rem;
  align-self: stretch;
  background-color: whitesmoke;
  font-family: 'Varela Round', sans-serif;
  border-radius: 3px;
  resize: none;
  font-size: 1.2rem;
  cursor: default;
}

/* Lists */
.list-load-template {
  list-style-type: none;
  padding: 0;
  display: flex;
  flex-direction: column;
}

.list-load-template-item {
  border-bottom: 1px solid black;
  display : flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  background-color: white;
}

.list-load-template-item:hover {
  cursor: pointer;
  background-color: var(--color-accent);
  border-radius: 4px;
}

/* Media Queries */
@media only screen and (min-width: 680px) {
  .wrap-timer-frame {
    min-height: 41.16rem;
    min-width: 41.16rem;
  }
  
  .wrap-focus-icons {
    gap: 1.12rem;
  }
  
  .text-focus-icons {
    font-size: 1.344rem;
  }
  
  .card-timer-container {
    grid-template-columns: 10.92rem 10.92rem 10.92rem;
    grid-template-rows: 2.1rem 16.8rem 3.36rem;
    margin: 4.2rem 0;
  }
  
  .btn-time-block {
    font-size: 1.47rem;
  }
  
  .wrap-time-block-selected {
    font-size: 1.68rem;
    padding: 0.42rem;
  }

  .btn-stop-and-go {
    padding: 0.84rem 2.1rem 0.42rem;
    font-size: 1.96rem;
  }

  .btn-reset {
    font-size: 1.96rem;
  }
  
  .btn-skip {
    font-size: 1.96rem;
  }

  .icon-focus-lemon {
    font-size: 1.68rem;
  }

  .App {
    max-width: 50.4rem;
  }

  .primary-header_logo {
    font-size: 2.24rem;
  }

  .popup-inner {
    width: 50%;
    padding: 5.6rem;
  }

  .link-button {
    font-size: 1.68rem;
    padding: 0.56rem;
  }
  
  .heading-error-text {
    font-size: 3.92rem;
  }

  .heading-time-display {
    font-size: 10.92rem;
  }

  .btn-dropdown {
    font-size: 3.36rem;
  }

  .wrap-dropdown-content {
    min-width: 168px;
  }

  .wrap-dropdown-content .wrap-dropdown-content-option {
    padding: 1.008rem 1.12rem;
  }

  .wrap-dropdown-content-option > p {
    font-size: 1.68rem;
    padding: 0.336rem;
  }

  .heading-task-container {
    font-size: 3.36rem;
  }

  .wrap-task-mainline {
    margin: 1.12rem 1.12rem 0.56rem;
  }
  
  .heading-task {
    font-size: 2.24rem;
  }
  
  .text-duration {
    font-size: 2.24rem;
  }

  .btn-edit {
    font-size: 1.456rem;
  }

  .input-task-notes {
    padding: 0.896rem 1.68rem;
    margin: 0.56rem 1.12rem;
    font-size: 1.68rem;
  }

  .btn-submit {
    margin: 2.24rem 0 0;
    height: 3.36rem;
    font-size: 2.24rem;
  }

  .text-submission-error {
    font-size: 1.68rem;
  }

  .heading-login {
    font-size: 3.36rem;
  }
  
  .input-login {
    margin: 0 0 1.12rem;
    padding: 0.448rem;
    height: 3.92rem;
    font-size: 2.016rem;
  }
  
  .login-submit {
    margin: 1.12rem 0 2.8rem;
    height: 3.36rem;
  }
  
  .wrap-login-footer {
    margin-top: 2.24rem;
  }
  
  .wrap-footer-text {
    font-size: 1.68rem;
  }

  .error {
    font-size: 1.68rem;
  }

  .heading-load-template {
    font-size: 3.36rem;
  }
  
  .btn-load-template-edit {
    font-size: 1.68rem;
  }

  .wrap-load-template-name {
    font-size: 2.24rem;
  }

  .wrap-edit-task-mainline {
    margin: 1.12rem 5.488rem 0.56rem 0.896rem;
  }

  .input-edit-task-heading {
    font-size: 2.24rem;
  }

  .input-edit-task-duration {
    font-size: 2.24rem;
    width: 5.6rem;

  }

  .input-edit-task-save {
    margin: 0.12rem 2.016rem 0;
    padding: 0.56rem;
    font-size: 1.456rem;
  }

  .input-edit-task-notes {
    padding: 0.896rem 1.68rem;
    margin: 0.56rem 1.12rem 0;
    font-size: 1.68rem;
  }
  
  .btn-edit-task-close {
    right: 1.68rem;
    padding: 0.616rem 0.616rem 0.56rem;
    font-size: 1.456rem;
  }

  .btn-edit-task-delete {
    bottom: 0.056rem;
    right: 7.28rem;
    font-size: 1.456rem;
  }

  .btn-delete-account {
    padding: 0.56rem 2.24rem;
    font-size: 2.24rem;
  } 

  .card-add {
    padding: 0.7rem 0.7rem;
    margin: 0.42rem 0;
  }

  .btn-close-addTask {
    right: 1.68rem;
    padding: 0.616rem 0.616rem 0.196rem;
    font-size: 1.456rem;
  }

  .btn-dropdown.user-opts {
    padding: 0.77rem 0.77rem 0.98rem;
    font-size: 1.568rem;
  }
}

@media only screen and (min-width: 1441px) {
  .wrap-timer-frame {
    min-height: 57.624rem;
    min-width: 57.624rem;
  }
  
  .wrap-focus-icons {
    gap: 1.568rem;
  }
  
  .text-focus-icons {
    font-size: 1.881rem;
  }
  
  .card-timer-container {
    grid-template-columns: 15.288rem 15.288rem 15.288rem;
    grid-template-rows: 2.94rem 23.52rem 4.704rem;
    margin: 5.88rem 0;
  }
  
  .btn-time-block {
    font-size: 2.058rem;
  }
  
  .wrap-time-block-selected {
    font-size: 2.352rem;
    padding: 0.588rem;
  }

  .btn-stop-and-go {
    padding: 1.176rem 2.94rem 0.588rem;
    font-size: 2.744rem;
  }

  .btn-reset {
    font-size: 2.744rem;
  }
  
  .btn-skip {
    font-size: 2.744rem;
  }

  .icon-focus-lemon {
    font-size: 2.352rem;
  }

  .App {
    max-width: 70.56rem;
  }

  .primary-header_logo {
